import hyperform from 'hyperform';

const formValidationI18n = {
  ru: {
    TextTooLong:
      ',     %l    (   %l ).',
    TextTooShort:
      ',    %l  (   %l ).',
    ValueMissing: ',   .',
    CheckboxMissing: ',  ,    .',
    RadioMissing: ',     .',
    FileMissing: ',  .',
    SelectMissing: ',    .',
    InvalidEmail: ',    .',
    InvalidURL: ',  -.',
    InvalidDate: ',  ',
    PatternMismatch: ',   .',
    PatternMismatchWithTitle: ',   : %l.',
    NumberRangeOverflow: ',  ,    %l.',
    DateRangeOverflow: ',  ,   %l.',
    TimeRangeOverflow: ',  ,   %l.',
    NumberRangeUnderflow: ',  ,   %l.',
    DateRangeUnderflow: ',  ,  ,  %l.',
    TimeRangeUnderflow: ',  ,  ,  %l.',
    StepMismatch:
      ',   .     %l  %l',
    StepMismatchOneValue:
      ',   .    %l.',
    BadInputNumber: ',  ',
    'Please match the requested type.': ',   .',
    'Please comply with all requirements.': ',    .',
    'Please lengthen this text to %l characters or more (you are currently using %l characters).':
      ',     %l    (  %l ).',
    'Please use the appropriate format.': ',   .',
    'Please enter a comma separated list of email addresses.':
      ',       ',
    'Please select a file of the correct type.': ',    .',
    'Please select one or more files.': ',     .',
    'any value': ' ',
    'any valid value': '  '
  }
};

class Form {
  constructor(el) {
    this.lang = 'ru';
    this.el = el;
    this.formValidationClasses = {
      warning: 'form-field__warning-msg',
      valid: 'form-field_valid',
      invalid: 'form-field_invalid',
      validated: 'form-field_validated'
    };
    this.init();
  }

  static getID() {
    return 'form';
  }

  init() {
    hyperform(this.el, {
      classes: this.formValidationClasses,
      validateNameless: true,
      preventImplicitSubmit: true
    });
    hyperform.addTranslation(this.lang, formValidationI18n[this.lang]);
    hyperform.setLanguage(this.lang);
  }
}

export { Form };
